import React from 'react';
import PanelWrapper from '../components/ui/PanelWrapper';
import Card from '../components/ui/card';
import Button from '../components/ui/button';
import { Link } from 'react-router-dom';
import './FingeringPage.css';

interface TestFingeringPageProps {}

const TestFingeringPage: React.FC<TestFingeringPageProps> = () => {
  console.log('TestFingeringPage component rendering');
  
  return (
    <PanelWrapper className="fingering-wrapper">
      <div className="fingering-container">
        <h1 className="page-title">Test Fingering Charts Page</h1>
        
        <Card>
          <p>This is a simplified test version of the Fingering Charts page.</p>
          <div className="home-button">
            <Link to="/">
              <Button style={{ 
                backgroundColor: 'var(--color-primary)', 
                color: 'white', 
                minWidth: '150px',
                fontWeight: 'bold',
                padding: '10px 20px',
                fontSize: '16px',
                border: 'none'
              }}>
                Back to Menu
              </Button>
            </Link>
          </div>
        </Card>
      </div>
    </PanelWrapper>
  );
};

export default TestFingeringPage; 