import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import MusicalStaff from '../components/MusicalStaff';
import { Clef, Note, NoteData, Instrument, TRUMPET_NOTES, TROMBONE_NOTES, RECORDER_NOTES, OCARINA_NOTES } from '../types';
import './FingeringPage.css';

const FingeringPage: React.FC = () => {
  const [selectedInstrument, setSelectedInstrument] = useState<Instrument>('trumpet');
  const [selectedClef, setSelectedClef] = useState<Clef>('treble');
  const [selectedNote, setSelectedNote] = useState<NoteData | null>(null);
  const [availableNotes, setAvailableNotes] = useState<NoteData[]>([]);
  const [viewMode, setViewMode] = useState<'full' | 'single'>('full');
  
  useEffect(() => {
    // Get available notes based on selected instrument
    let instrumentNotes: NoteData[] = [];
    
    switch (selectedInstrument) {
      case 'trumpet':
        instrumentNotes = TRUMPET_NOTES;
        break;
      case 'trombone':
        instrumentNotes = TROMBONE_NOTES;
        break;
      case 'recorder':
        instrumentNotes = RECORDER_NOTES;
        break;
      case 'ocarina':
        instrumentNotes = OCARINA_NOTES;
        break;
      default:
        instrumentNotes = TRUMPET_NOTES;
    }
    
    setAvailableNotes(instrumentNotes);
    setSelectedNote(null); // Reset selected note when instrument changes
  }, [selectedInstrument]);
  
  const handleNoteSelect = (note: NoteData) => {
    console.log(`Note selected: MIDI ${note.midiNumber || note.note?.midiNumber}`);
    
    // Find the note in the available notes by MIDI number
    const midiNumber = note.midiNumber || note.note?.midiNumber;
    const foundNote = availableNotes.find(n => 
      n.note?.midiNumber === midiNumber || 
      n.midiNumber === midiNumber
    );
    
    if (foundNote) {
      const noteName = foundNote.note?.name || "";
      const noteOctave = foundNote.note?.octave || 0;
      console.log(`Found matching note: ${noteName}${noteOctave}, MIDI: ${midiNumber}`);
      console.log('Fingering data:', foundNote.fingering);
      setSelectedNote(foundNote);
    } else {
      console.log(`No fingering data found for MIDI ${midiNumber} for ${selectedInstrument}`);
      setSelectedNote(null);
    }
  };
  
  // Convert NoteData array into the format expected by MusicalStaff
  const prepareNotesForStaff = (noteDataArray: NoteData[]): NoteData[] => {
    return noteDataArray.map(noteData => ({
      ...noteData,
      midiNumber: noteData.note?.midiNumber || noteData.midiNumber
    }));
  };

  // Navigate to previous or next note
  const navigateToPreviousNote = () => {
    if (!selectedNote || availableNotes.length === 0) return;
    
    const currentIndex = availableNotes.findIndex(n => 
      (n.note?.midiNumber === selectedNote.note?.midiNumber) || 
      (n.midiNumber === selectedNote.midiNumber)
    );
    
    if (currentIndex > 0) {
      setSelectedNote(availableNotes[currentIndex - 1]);
    }
  };
  
  const navigateToNextNote = () => {
    if (!selectedNote || availableNotes.length === 0) return;
    
    const currentIndex = availableNotes.findIndex(n => 
      (n.note?.midiNumber === selectedNote.note?.midiNumber) || 
      (n.midiNumber === selectedNote.midiNumber)
    );
    
    if (currentIndex < availableNotes.length - 1) {
      setSelectedNote(availableNotes[currentIndex + 1]);
    }
  };
  
  return (
    <div className="fingering-page-container">
      <h1>Fingering Charts</h1>
      
      <div className="controls-container">
        <div className="instrument-selector">
          <label htmlFor="instrument-select">Instrument:</label>
          <select 
            id="instrument-select"
            value={selectedInstrument}
            onChange={(e) => setSelectedInstrument(e.target.value as Instrument)}
          >
            <option value="trumpet">Trumpet</option>
            <option value="trombone">Trombone</option>
            <option value="recorder">Recorder</option>
            <option value="ocarina">Ocarina</option>
          </select>
        </div>
        
        <div className="clef-selector">
          <label htmlFor="clef-select">Clef:</label>
          <select 
            id="clef-select"
            value={selectedClef}
            onChange={(e) => setSelectedClef(e.target.value as Clef)}
          >
            <option value="treble">Treble</option>
            <option value="bass">Bass</option>
          </select>
        </div>
      </div>
      
      <div className="view-mode-toggle">
        <button 
          className={viewMode === 'full' ? 'active' : ''} 
          onClick={() => setViewMode('full')}
        >
          Full Staff View
        </button>
        <button 
          className={viewMode === 'single' ? 'active' : ''} 
          onClick={() => setViewMode('single')}
        >
          Single Note View
        </button>
      </div>
      
      <div className="staff-container-wrapper">
        <div className="instructions">
          <p>Click on any note to see its fingering diagram</p>
        </div>
        <div className="staff-container">
          <div style={{ overflow: 'auto', whiteSpace: 'nowrap', minWidth: '100%' }}>
            <MusicalStaff
              notes={prepareNotesForStaff(availableNotes)}
              selectedNote={selectedNote}
              clef={selectedClef}
              onNoteSelect={handleNoteSelect}
              viewMode={viewMode}
            />
          </div>
        </div>
        
        <div className="note-nav-buttons">
          <button 
            className="nav-button" 
            onClick={navigateToPreviousNote}
            disabled={!selectedNote || availableNotes.findIndex(n => 
              (n.note?.midiNumber === selectedNote?.note?.midiNumber) || 
              (n.midiNumber === selectedNote?.midiNumber)) <= 0}
          >
            <span className="nav-button-icon">⬅️</span> Previous Note
          </button>
          <button 
            className="nav-button" 
            onClick={navigateToNextNote}
            disabled={!selectedNote || availableNotes.findIndex(n => 
              (n.note?.midiNumber === selectedNote?.note?.midiNumber) || 
              (n.midiNumber === selectedNote?.midiNumber)) >= availableNotes.length - 1}
          >
            Next Note <span className="nav-button-icon">➡️</span>
          </button>
        </div>
      </div>
      
      <div className="fingering-display-wrapper">
        <div className="fingering-display-card">
          <h3 className="fingering-title">Fingering Diagram</h3>
          <div className="selected-note-info">
            {selectedNote ? (
              <span className="selected-note-name">
                {selectedNote.note?.name}{selectedNote.note?.octave} 
                (MIDI: {selectedNote.note?.midiNumber || selectedNote.midiNumber})
              </span>
            ) : (
              <span className="no-selection">No note selected</span>
            )}
          </div>
          
          <div className="fingering-display">
            {selectedNote ? (
              <>
                {selectedInstrument === 'trumpet' && (
                  <div className="trumpet-fingering">
                    <div className="valves">
                      <div className={`valve ${selectedNote.fingering.valve1 ? 'pressed' : ''}`}>1</div>
                      <div className={`valve ${selectedNote.fingering.valve2 ? 'pressed' : ''}`}>2</div>
                      <div className={`valve ${selectedNote.fingering.valve3 ? 'pressed' : ''}`}>3</div>
                    </div>
                    <div className="fingering-description">
                      {selectedNote.fingering.description || 'Press the highlighted valves'}
                    </div>
                  </div>
                )}
                
                {selectedInstrument === 'trombone' && (
                  <div className="trombone-position">
                    <div className="slide-visual">
                      <div 
                        className="slide-indicator"
                        style={{ left: `${(selectedNote.fingering.position ? (selectedNote.fingering.position - 1) : 0) * 12.5}%` }}
                      ></div>
                      <div className="position-numbers">
                        {[1, 2, 3, 4, 5, 6, 7].map(pos => (
                          <div 
                            key={pos} 
                            className={`position-mark ${selectedNote.fingering.position === pos ? 'active' : ''}`}
                          >
                            {pos}
                          </div>
                        ))}
                      </div>
                    </div>
                    <div className="fingering-description">
                      {selectedNote.fingering.description || `Slide Position: ${selectedNote.fingering.position}`}
                    </div>
                  </div>
                )}
                
                {selectedInstrument === 'recorder' && (
                  <div className="recorder-fingering">
                    <div className="fingering-diagram">
                      {/* Display covered holes based on fingering data */}
                      <div className="recorder-holes">
                        {Array.from({ length: 7 }).map((_, index) => (
                          <div 
                            key={index} 
                            className={`recorder-hole ${selectedNote.fingering.holes?.includes(index) ? 'covered' : ''}`}
                          >
                            {index === 0 ? 'T' : index}
                          </div>
                        ))}
                      </div>
                      <div className="fingering-description">
                        {selectedNote.fingering.description || 'Cover the highlighted holes'}
                      </div>
                    </div>
                  </div>
                )}
                
                {selectedInstrument === 'ocarina' && (
                  <div className="ocarina-fingering">
                    <div className="fingering-diagram">
                      {/* Display covered holes based on fingering data */}
                      <div className="ocarina-holes">
                        {Array.from({ length: 12 }).map((_, index) => (
                          <div 
                            key={index + 1} 
                            className={`ocarina-hole ${selectedNote.fingering.ocarinaHoles?.includes(index + 1) ? 'covered' : ''}`}
                          >
                            {index + 1}
                          </div>
                        ))}
                      </div>
                      <div className="fingering-description">
                        {selectedNote.fingering.description || 'Cover the highlighted holes'}
                      </div>
                    </div>
                  </div>
                )}
              </>
            ) : (
              <div className="no-selection-message">
                <p>Select a note on the staff to see the fingering</p>
              </div>
            )}
          </div>
        </div>
      </div>
      
      <div className="debug-info" title="Development information">
        Debug - Instrument: {selectedInstrument}, Clef: {selectedClef}
        {selectedNote && (
          <span>
            , Note: {selectedNote.note?.name}{selectedNote.note?.octave} 
            (MIDI: {selectedNote.note?.midiNumber || selectedNote.midiNumber})
          </span>
        )}
      </div>
      
      <div className="back-button-container">
        <Link to="/" className="back-button">Back to Home</Link>
      </div>
    </div>
  );
};

export default FingeringPage; 