import React, { useRef, useState, useEffect, useCallback } from 'react';
import * as OSMD from 'opensheetmusicdisplay';
import { debounce } from 'lodash';
import { Note, Clef, NoteData } from '../types';
import '../App.css';

// Define the component props
export interface MusicalStaffProps {
  notes: NoteData[];
  selectedNote: NoteData | null;
  clef: Clef;
  onNoteSelect?: (note: NoteData) => void;
  keyName?: string;
  colorByDegree?: boolean;
  rootNote?: string;
  viewMode?: 'full' | 'single';
}

// Color map for scale degrees (Boomwhacker colors)
const DEGREE_COLORS: { [key: number]: string } = {
  0: '#FF0000', // Red (C)
  1: '#FF7F00', // Orange (C#/Db)
  2: '#FFFF00', // Yellow (D)
  3: '#00FF00', // Light green (D#/Eb)
  4: '#00FF7F', // Green (E)
  5: '#00FFFF', // Cyan (F)
  6: '#007FFF', // Light blue (F#/Gb)
  7: '#0000FF', // Blue (G)
  8: '#7F00FF', // Purple (G#/Ab)
  9: '#FF00FF', // Magenta (A)
  10: '#FF007F', // Pink (A#/Bb)
  11: '#A52A2A', // Brown (B)
};

const MusicalStaff: React.FC<MusicalStaffProps> = ({
  notes,
  selectedNote,
  clef,
  onNoteSelect,
  keyName = 'C',
  colorByDegree = false,
  rootNote,
  viewMode = 'full'
}) => {
  const containerRef = useRef<HTMLDivElement>(null);
  const osmdRef = useRef<OSMD.OpenSheetMusicDisplay | null>(null);
  const [hasRendered, setHasRendered] = useState(false);
  const handleClickRef = useRef<((e: MouseEvent) => void) | null>(null);
  const lastClickTimeRef = useRef<number>(0);
  
  // Generate MusicXML from notes
  const generateMusicXml = useCallback(() => {
    // Get key signature value
    const getKeySignatureByName = (keyName: string): number => {
      // Map of key names to fifths
      const keyMap: { [key: string]: number } = {
        'C': 0, 'G': 1, 'D': 2, 'A': 3, 'E': 4, 'B': 5, 'F#': 6, 'C#': 7,
        'F': -1, 'Bb': -2, 'Eb': -3, 'Ab': -4, 'Db': -5, 'Gb': -6, 'Cb': -7
      };
      return keyMap[keyName] || 0;
    };
    
    // Convert MIDI number to MusicXML note info
    const getMusicXmlNoteInfo = (midiNumber: number, fifths: number) => {
      const noteNames = fifths >= 0 ? 
        ['C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#', 'A', 'A#', 'B'] : 
        ['C', 'Db', 'D', 'Eb', 'E', 'F', 'Gb', 'G', 'Ab', 'A', 'Bb', 'B'];
      
      const pitchClass = midiNumber % 12;
      const octave = Math.floor(midiNumber / 12) - 1;
      const noteName = noteNames[pitchClass];
      
      // Extract step and alter values for MusicXML
      let step = noteName[0];
      let alter = 0;
      
      if (noteName.length > 1) {
        if (noteName[1] === '#') alter = 1;
        else if (noteName[1] === 'b') alter = -1;
      }
      
      return { step, alter, octave };
    };
    
    // Get key signature
    const fifths = keyName ? getKeySignatureByName(keyName) : 0;
    
    // Skip rendering if no notes
    if (!notes || notes.length === 0) {
      return `<?xml version="1.0" encoding="UTF-8"?>
        <score-partwise>
          <part id="P1">
            <measure number="1">
              <attributes>
                <divisions>1</divisions>
                <key><fifths>0</fifths></key>
                <clef><sign>${clef === 'treble' ? 'G' : 'F'}</sign><line>${clef === 'treble' ? '2' : '4'}</line></clef>
              </attributes>
              <note>
                <rest/>
                <duration>4</duration>
              </note>
            </measure>
          </part>
        </score-partwise>`;
    }
    
    // Sort notes by MIDI number for proper rendering
    const sortedNotes = [...notes].sort((a, b) => {
      const midiA = a.midiNumber || a.note?.midiNumber || 0;
      const midiB = b.midiNumber || b.note?.midiNumber || 0;
      return midiA - midiB;
    });
    
    // If in single view mode and there's a selected note, only show that note
    const notesToRender = viewMode === 'single' && selectedNote ? 
      [selectedNote] : 
      sortedNotes;
    
    // Start building MusicXML
    let musicXml = `<?xml version="1.0" encoding="UTF-8"?>
<!DOCTYPE score-partwise PUBLIC "-//Recordare//DTD MusicXML 3.1 Partwise//EN" "http://www.musicxml.com/dtd/partwise.dtd">
<score-partwise>
  <part-list>
    <score-part id="P1">
      <part-name>Music</part-name>
    </score-part>
  </part-list>
  <part id="P1">
    <measure number="1">
      <attributes>
        <divisions>1</divisions>
        <key>
          <fifths>${fifths}</fifths>
        </key>
        <time>
          <beats>4</beats>
          <beat-type>4</beat-type>
        </time>
        <clef>
          <sign>${clef === 'bass' ? 'F' : 'G'}</sign>
          <line>${clef === 'bass' ? '4' : '2'}</line>
        </clef>
      </attributes>`;
    
    // Add notes to the MusicXML
    notesToRender.forEach((note) => {
      // Skip notes without MIDI numbers
      if (note.midiNumber === undefined) return;
      
      const noteInfo = getMusicXmlNoteInfo(note.midiNumber, fifths);
      const isSelected = selectedNote && selectedNote.midiNumber === note.midiNumber;
      
      // Determine note color
      let noteColor = '#000000'; // Default black
      
      if (isSelected) {
        noteColor = '#FF0000'; // Red for selected note
      } else if (colorByDegree && rootNote) {
        // Apply Boomwhacker colors based on scale degree
        const pitchClass = note.midiNumber % 12;
        const rootPitchClass = parseInt(rootNote) % 12;
        const relativeDegree = (pitchClass - rootPitchClass + 12) % 12; // Normalize to 0-11
        
        noteColor = DEGREE_COLORS[relativeDegree] || '#000000';
      }
      
      musicXml += `
    <note id="note-${note.midiNumber}" default-x="${note.midiNumber * 5}">
      <pitch>
        <step>${noteInfo.step}</step>
        ${noteInfo.alter !== 0 ? `<alter>${noteInfo.alter}</alter>` : ''}
        <octave>${noteInfo.octave}</octave>
      </pitch>
      <duration>1</duration>
      <type>quarter</type>
      <notehead color="${noteColor}">normal</notehead>
      <lyric default-y="-80" justify="center" placement="below">
        <text>${note.midiNumber}</text>
      </lyric>
    </note>`;
    });
    
    // Close the MusicXML document
    musicXml += `
    </measure>
  </part>
</score-partwise>`;
    
    return musicXml;
  }, [notes, selectedNote, clef, rootNote, colorByDegree, keyName, viewMode]);
  
  // Click handler for note selection
  const setupClickHandler = useCallback(() => {
    if (!containerRef.current || !onNoteSelect) return;
    
    // Remove any existing handlers
    if (handleClickRef.current) {
      containerRef.current.removeEventListener('click', handleClickRef.current);
    }
    
    // Create new handler
    handleClickRef.current = (event: MouseEvent) => {
      // Debounce rapid clicks
      const now = Date.now();
      if (now - lastClickTimeRef.current < 300) return;
      lastClickTimeRef.current = now;
      
      // Get clicked element and ensure it's an Element
      const target = event.target as Element | null;
      
      // Try to find midi number from text content
      if (target && target.textContent) {
        const midiNumber = parseInt(target.textContent);
        if (!isNaN(midiNumber)) {
          console.log(`Selected MIDI note: ${midiNumber}`);
          const foundNote = notes.find(n => n.midiNumber === midiNumber);
          if (foundNote && onNoteSelect) {
            onNoteSelect(foundNote);
          }
          return;
        }
      }
      
      // If no direct MIDI number, look for closest note
      const svg = containerRef.current?.querySelector('svg');
      if (!svg) return;
      
      const textElements = Array.from(svg.querySelectorAll('text'));
      let closestElement: Element | null = null;
      let minDistance = Number.MAX_VALUE;
      
      textElements.forEach(elem => {
        const elemContent = elem.textContent;
        if (elemContent && !isNaN(parseInt(elemContent))) {
          const rect = elem.getBoundingClientRect();
          const centerX = rect.left + rect.width / 2;
          const centerY = rect.top + rect.height / 2;
          
          const distance = Math.sqrt(
            Math.pow(event.clientX - centerX, 2) + 
            Math.pow(event.clientY - centerY, 2)
          );
          
          if (distance < minDistance && distance < 50) {
            minDistance = distance;
            closestElement = elem;
          }
        }
      });
      
      if (closestElement) {
        // Use type assertion to tell TypeScript that closestElement is an Element with textContent
        const element = closestElement as { textContent?: string };
        if (element.textContent) {
          const midiNumber = parseInt(element.textContent);
          if (!isNaN(midiNumber)) {
            console.log(`Selected closest MIDI note: ${midiNumber}`);
            const foundNote = notes.find(n => n.midiNumber === midiNumber);
            if (foundNote && onNoteSelect) {
              onNoteSelect(foundNote);
            }
          }
        }
      }
    };
    
    // Add the click handler
    containerRef.current.addEventListener('click', handleClickRef.current);
    
    // Return cleanup function
    return () => {
      if (containerRef.current && handleClickRef.current) {
        containerRef.current.removeEventListener('click', handleClickRef.current);
      }
    };
  }, [onNoteSelect, notes]);
  
  // Initial OSMD rendering
  useEffect(() => {
    const renderScore = async () => {
      if (!containerRef.current) return;
      
      try {
        // Clear previous instance
        if (osmdRef.current) {
          containerRef.current.innerHTML = '';
        }
        
        // Create new OSMD instance
        osmdRef.current = new OSMD.OpenSheetMusicDisplay(containerRef.current);
        
        // Configure OSMD options
        osmdRef.current.setOptions({
          backend: "svg",
          drawTitle: false,
          drawSubtitle: false,
          drawPartNames: false,
          drawMeasureNumbers: false,
          drawTimeSignatures: false,
          autoResize: true,
          followCursor: false,
          defaultFontFamily: "Arial",
          defaultColorNotehead: "#FFFFFF", // Make notes more visible with white color
          defaultColorStem: "#FFFFFF", // White stems
          defaultColorRest: "#FFFFFF", // White rests
          defaultColorLabel: "#FFFFFF", // White text
          drawHiddenNotes: false
        });
        
        const musicXml = generateMusicXml();
        await osmdRef.current.load(musicXml);
        await osmdRef.current.render();
        
        // Add interactive elements after rendering
        const svg = containerRef.current.querySelector('svg');
        if (svg) {
          // Make sure svg takes full width of container
          svg.setAttribute('width', '100%');
          svg.setAttribute('preserveAspectRatio', 'xMinYMid meet');
          
          // Add a translucent overlay to make clicking easier
          const svgns = "http://www.w3.org/2000/svg";
          const overlay = document.createElementNS(svgns, 'rect');
          overlay.setAttribute('width', '100%');
          overlay.setAttribute('height', '100%');
          overlay.setAttribute('fill', 'transparent');
          overlay.setAttribute('class', 'click-overlay');
          svg.appendChild(overlay);
          
          // Add tooltip to indicate notes are clickable
          const tooltip = document.createElement('div');
          tooltip.className = 'staff-tooltip';
          tooltip.textContent = 'Click on a note to select it';
          tooltip.style.position = 'absolute';
          tooltip.style.top = '10px';
          tooltip.style.left = '50%';
          tooltip.style.transform = 'translateX(-50%)';
          tooltip.style.backgroundColor = 'rgba(0,0,0,0.7)';
          tooltip.style.color = 'white';
          tooltip.style.padding = '5px 10px';
          tooltip.style.borderRadius = '4px';
          tooltip.style.fontSize = '14px';
          tooltip.style.zIndex = '5';
          tooltip.style.pointerEvents = 'none';
          containerRef.current.appendChild(tooltip);
          
          // Make the tooltip disappear after 5 seconds
          setTimeout(() => {
            if (tooltip && tooltip.parentNode) {
              tooltip.style.opacity = '0';
              tooltip.style.transition = 'opacity 1s ease';
              setTimeout(() => {
                if (tooltip && tooltip.parentNode) {
                  tooltip.parentNode.removeChild(tooltip);
                }
              }, 1000);
            }
          }, 5000);
          
          // Make sure notes have high contrast
          const noteheads = svg.querySelectorAll('.vf-notehead');
          noteheads.forEach((notehead: Element) => {
            if (notehead instanceof SVGElement) {
              notehead.style.cursor = 'pointer';
              notehead.style.fill = 'white';
              notehead.style.stroke = 'rgba(0,0,0,0.3)';
              notehead.style.strokeWidth = '0.5px';
            }
          });
          
          // Make text labels more visible
          const texts = svg.querySelectorAll('text');
          texts.forEach((text: Element) => {
            if (text instanceof SVGElement) {
              text.style.fill = 'white';
              text.style.fontWeight = 'bold';
              text.style.textShadow = '0px 0px 2px rgba(0,0,0,0.7)';
            }
          });
        }
        
        // Mark as rendered so we know we're ready for click handlers
        setHasRendered(true);
        
        // Add note selection click handler
        setupClickHandler();
        
        console.log("OSMD staff rendered");
        
      } catch (error) {
        console.error('Error rendering MusicalStaff:', error);
      }
    };
    
    renderScore();
    
    // Cleanup on unmount
    return () => {
      if (containerRef.current && handleClickRef.current) {
        containerRef.current.removeEventListener('click', handleClickRef.current);
      }
    };
  }, [notes, selectedNote, rootNote, colorByDegree, clef, keyName, generateMusicXml, setupClickHandler]);
  
  return (
    <div 
      className="musical-staff"
      ref={containerRef} 
      style={{ 
        width: '100%',
        minHeight: '150px', 
        position: 'relative',
        cursor: 'pointer'
      }}
    >
      <div className="staff-loading-indicator" 
        style={{
          display: osmdRef.current ? 'none' : 'flex',
          position: 'absolute',
          top: '0',
          left: '0',
          width: '100%',
          height: '100%',
          alignItems: 'center',
          justifyContent: 'center',
          backgroundColor: 'rgba(35, 42, 60, 0.6)',
          color: 'white',
          fontWeight: 'bold'
        }}
      >
        Loading music staff...
      </div>
    </div>
  );
};

export default MusicalStaff; 