import { TRUMPET_NOTES_DATA, TrumpetNoteData, getValvesForNote } from '../data/trumpetData';

export type Instrument = 'trumpet' | 'trombone' | 'recorder' | 'ocarina';
export type Clef = 'treble' | 'bass';
export type InstrumentPitch = 'C' | 'Bb' | 'Eb' | 'F' | 'concert';

// Represents a musical note
export interface Note {
  name: string;
  octave: number;
  midiNumber: number;
}

// Fingering information for different instruments
export interface Fingering {
  // Properties for trumpet
  valve1?: boolean;
  valve2?: boolean;
  valve3?: boolean;
  
  // Property for trombone
  position?: number;
  
  // Property for recorder
  holes?: number[];
  
  // Property for ocarina
  ocarinaHoles?: number[];
  
  // General description for any instrument
  description?: string;
}

// Combines a note with its fingering information
export interface NoteData {
  note: Note;
  fingering: Fingering;
  midiNumber?: number; // For compatibility with MIDI mode
}

// Scale types
export type ScaleType = 'major' | 'minor' | 'pentatonic' | 'blues' | 'chromatic';

// Scale data
export interface Scale {
  root: string;
  type: ScaleType;
  notes: Note[];
}

// Mode information
export type AppMode = 'beginner' | 'scales' | 'chords' | 'practice' | 'fingering' | 'valveHero';

// Helper function to calculate note frequency from MIDI note number
const getNoteFrequency = (midiNote: number): number => {
  return 440 * Math.pow(2, (midiNote - 69) / 12);
};

// Helper function to get correct note name from MIDI note
export const getMidiNoteName = (midiNote: number): { name: string, octave: number } => {
  const noteNames = ['C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#', 'A', 'A#', 'B'];
  const octave = Math.floor(midiNote / 12) - 1;
  const noteName = noteNames[midiNote % 12];
  return { name: noteName, octave };
};

// Helper function to transpose MIDI note number
const transposeMidiNote = (midiNote: number, pitch: InstrumentPitch): number => {
  // For display purposes, we need the written pitch 
  // (what the player sees on the staff)
  const transpositionMap: Record<string, number> = {
    'C': 0,
    'Bb': 2,  // Bb instruments read a major second higher than concert
    'Eb': 9,  // Eb instruments read a minor sixth higher than concert
    'F': 12,  // F instruments read a major third higher than concert
    'concert': 0 // No transposition for concert pitch
  };
  return midiNote + (transpositionMap[pitch] || 0);
};

// Convert the trumpetData to the format expected by the application
export const TRUMPET_NOTES: NoteData[] = TRUMPET_NOTES_DATA.map(note => {
  return {
    note: {
      name: note.name,
      octave: note.octave,
      midiNumber: note.midiNote,
    },
    fingering: {
      valve1: note.valves.includes(1),
      valve2: note.valves.includes(2),
      valve3: note.valves.includes(3)
    }
  };
}).filter((item, index, self) => 
  // Filter out duplicate notes that have both sharp and flat names
  index === self.findIndex((t) => t.note.midiNumber === item.note.midiNumber)
);

